<template>
    <div class="game-setting">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>回复消息设置</span>
                    <div class="operate">
                        <span @click="handleOperate(1)">修改/保存</span>
                    </div>
                </div>
            </div>
            <div class="list">
                <div class="base-list-item">
                    <p class="list-item-title">游戏成功时回复</p>
                    <textarea v-model="gameSuccReply"></textarea>
                </div>
                <div class="base-list-item">
                    <p class="list-item-title">查分时回复</p>
                    <textarea v-model="queryIntegReply"></textarea>
                </div>
                <div class="base-list-item">
                    <p class="list-item-title">查流水统计时回复</p>
                    <textarea v-model="queryCountReply"></textarea>
                </div>
                <div class="base-list-item">
                    <p class="list-item-title">调整余额时回复</p>
                    <textarea v-model="changeBalanceReply"></textarea>
                </div>
                <div class="base-list-item">
                    <p class="list-item-title">自动返水时回复</p>
                    <textarea v-model="autoReturnReply"></textarea>
                </div>
                <div class="base-list-item">
                    <p class="list-item-title">回复消息设置可用变量</p>
                    <div class="poptip">
                        <p>{游戏}{当前玩法}{当期玩法}{当前玩法明细}{当期玩法明细}{期号}{简写期号}{剩余}{当期得分}{当日盈亏}{当期盈亏}{昵称}</p>
                        <p>{当日流水}{当前使用分数}{当期使用分数}{当前多行玩法}</p>
                        <p>{未开奖简写期号}{未开奖玩法}{未开奖玩法明细}{未开奖使用分数}</p>
                    </div>
                </div>
                <div class="list-item">
                    <div>
                        <CheckboxGroup v-model="systemOperateCheck"  class="system-operate-list">
                            <Checkbox v-for="(item, index) in systemOperate" :key="index" :label="item.label" class="system-operate-item">
                                <span>{{item.title}}</span>
                            </Checkbox>
                        </CheckboxGroup>
                    </div>
                    <div class="system-reply-list">
                        <div class="system-reply-item">
                            <span>取消订单时回复</span>
                            <i-input v-model="systemReply.cancelOrder" class="columns-item-input"></i-input>
                        </div>
                        <div class="system-reply-item">
                            <span>收到请求时回复</span>
                            <i-input v-model="systemReply.receiveReq" class="columns-item-input"></i-input>
                        </div>
                        <div class="system-reply-item">
                            <span>禁止取消时回复</span>
                            <i-input v-model="systemReply.notCancelOrder" class="columns-item-input"></i-input>
                        </div>
                        <div class="system-reply-item">
                            <span>命令错误时回复</span>
                            <i-input v-model="systemReply.commandError" class="columns-item-input"></i-input>
                        </div>
                        <div class="system-reply-item">
                            <span>余额不足时回复</span>
                            <i-input v-model="systemReply.insuffBalance" class="columns-item-input"></i-input>
                        </div>
                        <div class="system-reply-item">
                            <span>下注封盘时回复</span>
                            <i-input v-model="systemReply.sealing" class="columns-item-input"></i-input>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="system-reply">
            <p class="base-setting-save">可用变量说明</p>
            <div class="sys-variable-list">
                <div class="sys-item" v-for="(item, index) in sysVariables" :key="index">
                    <span>{{item.title}}</span>
                    <span>{{item.desc}}</span>
                </div>
            </div>
        </div>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
export default {
    data() {
        return {
            showLoading: false,
            canEdit1: false,
            canEdit2: false,
            autoBackWater: false,
            sysVariables: [
                {
                    title: '{游戏}',
                    desc: '当前游戏类型， 如：北京赛车'
                },
                {
                    title: '{期号}',
                    desc: '完整期号， 如重庆时时彩 第20150101120期'
                },
                {
                    title: '{剩余}',
                    desc: '玩家剩余分数，默认保留2位小数。可在封盘设定中全局修改'
                },
                {
                    title: '{当前玩法}',
                    desc: '玩家单次输入的命令生成的玩法内容，可以是一条或多条订单'
                },
                {
                    title: '{当期玩法}',
                    desc: '当前期所有玩法内容汇总'
                },
                {
                    title: '{当前玩法明细}',
                    desc: '如1/12/10，显示为“第一球1-10，第一球2-10'
                },
                {
                    title: '{当期玩法明细}',
                    desc: '当期所有玩法明细汇总'
                },
                {
                    title: '{简写期号}',
                    desc: '简写期号，如重庆时时彩 第120期'
                },
                {
                    title: '{当期得分}',
                    desc: '当期中奖分数'
                },
                {
                    title: '{当日得分}',
                    desc: '当日所有中奖分数总和'
                },
                {
                    title: '{当期盈亏}',
                    desc: '当期盈利分数'
                },
                {
                    title: '{当日盈亏}',
                    desc: '当日盈利分数总和'
                },
                {
                    title: '{当日流水}',
                    desc: '当日截止当前期所有有效订单分数总和'
                },
                {
                    title: '{当前使用分数}',
                    desc: '当前玩法的使用分数'
                },
                {
                    title: '{当期使用分数}',
                    desc: '当前期合计使用分数'
                },
                {
                    title: '{当前多行玩法}',
                    desc: '自动换行显示当前所有玩法内容'
                },
                {
                    title: '{未开奖简写期号}',
                    desc: '未开奖结算简写期号'
                },
                {
                    title: '{未开奖玩法}',
                    desc: '未开奖结算玩法'
                },
                {
                    title: '{未开奖玩法明细}',
                    desc: '未开奖结算玩法明细'
                },
                {
                    title: '{未开奖使用分数}',
                    desc: '未开奖结算使用分数'
                }
            ],
            gameSuccReply: '',
            queryIntegReply: '',
            queryCountReply: '',
            changeBalanceReply: '',
            autoReturnReply: '',
            systemReply: {
                notAddGamer: '',
                notCancelOrder: '',
                returnMsg: '',
                lockGamer: '',
                notStartGame: '',
                moveGamer: '',
                startGame: '',
                stopGame: '',
                commandError: '',
                receiveReq: '',
                cancelOrder: '',
                insuffBalance: '',
                sealing: '',
                noBetting: ''
            },
            systemOperateCheck: [],
            systemOperate: [
                {
                    label: 3,
                    title: '提交无效指令后@对方'
                },
                {
                    label: 4,
                    title: '下注时已封盘@对方'
                },
                {
                    label: 5,
                    title: '下注成功后@对方'
                },
                {
                    label: 6,
                    title: '取消玩法后@对方'
                },
                {
                    label: 7,
                    title: '收到查回请求后@对方'
                },
                {
                    label: 8,
                    title: '确认查回分数后@对方'
                },
                {
                    label: 9,
                    title: '下注积分不足时@对方'
                },
                {
                    label: 10,
                    title: '下注发生限额时@对方'
                }
            ],
            isLoading: false
        }
    },
    mounted() {
        let that = this;

        that.getData();
    },
    methods: {
        handleOperate(type) {
            let that = this;
            
            that.handleSave();
            // switch(type) {
            //     case 1:
            //         if (!that.canEdit1) {
            //             that.canEdit1 = true;
            //         } else {
            //             that.handleSave();
            //         }
            //     break;
            //     case 2:
            //         if (!that.canEdit2) {
            //             that.canEdit2 = true;
            //         } else {
            //             that.handleSave();
            //         }
            //     break;
            // }
        },
        handleSave() {
            let that = this,
                vData = {};

            vData = {
                "GameSuccess": that.gameSuccReply,
                "CheckScore": that.queryIntegReply,
                "CheckStream": that.queryCountReply,
                "Remainder": that.changeBalanceReply,
                "Backwater": that.autoReturnReply,
                // "NotAddedUserList": that.systemReply.notAddGamer,
                "ProhibitionCancel": that.systemReply.notCancelOrder,
                // "RevokeMessage": that.systemReply.returnMsg,
                "UserLock": that.systemReply.lockGamer,
                "GameNotStart": that.systemReply.notStartGame,
                // "RemoveGroupChat": that.systemReply.moveGamer,
                "GameStart": that.systemReply.startGame,
                "GameStop": that.systemReply.stopGame,
                "CommandError": that.systemReply.commandError,
                "ReceivingRequests": that.systemReply.receiveReq,
                "CancelOrder": that.systemReply.cancelOrder,
                "NotEnough": that.systemReply.insuffBalance,
                "Sealing": that.systemReply.sealing,
                "NotOrders": that.systemReply.noBetting,
                // "NoticeUserAddList": that.systemOperateCheck.findIndex(function(item) {return item === 1}) !== -1,
                // "NoticeDeleteUser": that.systemOperateCheck.findIndex(function(item) {return item === 2}) !== -1,
                "NoticeInvalidSub": that.systemOperateCheck.findIndex(function(item) {return item === 3}) !== -1,
                "NoticeSealing": that.systemOperateCheck.findIndex(function(item) {return item === 4}) !== -1,
                "NoticeBetSuccess": that.systemOperateCheck.findIndex(function(item) {return item === 5}) !== -1,
                "NoticeCancel": that.systemOperateCheck.findIndex(function(item) {return item === 6}) !== -1,
                "NoticeCheckRequest": that.systemOperateCheck.findIndex(function(item) {return item === 7}) !== -1,
                "NoticeConfirmRequest": that.systemOperateCheck.findIndex(function(item) {return item === 8}) !== -1,
                "NoticeInsufficientIntegral": that.systemOperateCheck.findIndex(function(item) {return item === 9}) !== -1,
                "NoticeQuota": that.systemOperateCheck.findIndex(function(item) {return item === 10}) !== -1,
                "SwitchBackwater": that.autoBackWater
                // "NoticeWithdraw": that.systemOperateCheck.findIndex(function(item) {return item === 11}) !== -1,
                // "NoticeNotJoin": that.systemOperateCheck.findIndex(function(item) {return item === 12}) !== -1
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/UpdateReplySetUp',
                    method: 'post',
                    data: vData
                }).then(function(data) {
                    that.isLoading = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    // that.canEdit1 = false;
                    // that.canEdit2 = false;
                    that.getData();
                })
            }
        },
        getData() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;

                that.axios({
                    url: '/api/Setup/GetReplySetUp'
                }).then(function(data) {
                    that.isLoading = false;

                    if (data.data.Status === 100) {
                        const result = data.data.Model;

                        that.gameSuccReply = result.GameSuccess;
                        that.queryIntegReply = result.CheckScore;
                        that.queryCountReply = result.CheckStream;
                        that.changeBalanceReply = result.Remainder;
                        that.autoReturnReply = result.Backwater;

                        that.systemReply = {
                            // notAddGamer: result.NotAddedUserList,
                            notCancelOrder: result.ProhibitionCancel,
                            // returnMsg: result.RevokeMessage,
                            lockGamer: result.UserLock,
                            notStartGame: result.GameNotStart,
                            // moveGamer: result.RemoveGroupChat,
                            startGame: result.GameStart,
                            stopGame: result.GameStop,
                            commandError: result.CommandError,
                            receiveReq: result.ReceivingRequests,
                            cancelOrder: result.CancelOrder,
                            insuffBalance: result.NotEnough,
                            sealing: result.Sealing,
                            noBetting: result.NotOrders
                        };

                        // result.NoticeUserAddList && that.systemOperateCheck.push(1);
                        // result.NoticeDeleteUser  && that.systemOperateCheck.push(2);
                        result.NoticeInvalidSub  && that.systemOperateCheck.push(3);
                        result.NoticeSealing  && that.systemOperateCheck.push(4);
                        result.NoticeBetSuccess  && that.systemOperateCheck.push(5);
                        result.NoticeCancel  && that.systemOperateCheck.push(6);
                        result.NoticeCheckRequest  && that.systemOperateCheck.push(7);
                        result.NoticeConfirmRequest  && that.systemOperateCheck.push(8);
                        result.NoticeInsufficientIntegral  && that.systemOperateCheck.push(9);
                        result.NoticeQuota  && that.systemOperateCheck.push(10);
                        that.autoBackWater = result.SwitchBackwater;
                        // result.NoticeWithdraw  && that.systemOperateCheck.push(11);
                        // result.NoticeNotJoin  && that.systemOperateCheck.push(12);
                    }
                    that.showLoading = false;
                })
            }
        }
    },
}
</script>

<style lang="scss">
.game-setting {
    position: relative;
    .content {
        flex-direction: column;

        .list-header {
            padding-top: 10px;

            background-color: #02b0d5;
            .list-title {
                justify-content: space-between;

                width: 100%;

                overflow: hidden;

                .operate {
                    & > span {
                        display: inline-block;

                        margin-right: 5px;

                        width: 116px;
                        height: 28px;
                        line-height: 28px;

                        // background-color: rgb(102, 153, 204);
                        // border-radius: 4px;
                        // border: solid 1px rgb(102, 102, 102);
                        background-color: #00e14a;
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 255, 158, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e903c;
                        text-align: center;
                        cursor: pointer;
                        color: #fff;
                    }
                }
            }
        }

        .list {
            display: flex;
            flex-wrap: wrap;

            .base-list-item {
                margin-top: 10px;
                margin-right: 15px;

                width: 220px;
                height: 140px;

                border: solid 1px #d7d7d7;
                overflow: hidden;

                .list-item-title {
                    padding-left: 5px;

                    height: 40px;
                    line-height: 40px;

                    background-color: rgb(228, 228, 228);
                    text-align: left;
                }

                & > textarea {
                    width: 100%;
                    height: 100px;

                    border: none;
                    overflow: auto;
                }
            }

            .base-list-item:nth-child(n+5) {
                height: 100px;

                & > textarea {
                    height: 60px;
                }
            }

            .base-list-item:nth-child(4n+4) {
                margin-right: 0;
            }

            .base-list-item:nth-child(6) {
                margin-right: 0;

                width: 690px;

                .list-item-title {
                    text-align: center;
                }

                .poptip {
                    font-size: 12px;

                    & > p {
                        padding-left: 5px;

                        text-align: left;
                    }
                }
            }

            .list-item {
                margin-top: 10px;

                background-color: rgb(228, 228, 228);
                border: 1px solid rgb(215, 215, 215);

                .system-operate-list {
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;

                    margin: 0 5px;

                    height: 90px;

                    border-bottom: solid 1px #bcbcbc;

                    & > * {
                        flex: 1 1 22%;

                        padding-left: 15px;

                        text-align: left;
                    }
                }

                .system-reply-list {
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;

                    padding: 0 20px;

                    height: 115px;

                    .system-reply-item {
                        flex: 1 1 50%;
                        display: flex;
                        align-items: center;

                        & > span {
                            width: 100px;
                        }

                        & > *:last-child {
                            width: 320px;
                        }
                    }
                }
            }
        }
    }

    .system-reply {
        margin-top: 10px;

        border: solid 1px #d7d7d7;

        .base-setting-save {
            height: 30px;
            line-height: 30px;

            background-color: #e4e4e4;
        }

        .sys-variable-list {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;

            font-size: 12px;
            background-color: #f2f2f2;

            .sys-item {
                display: flex;
                align-items: center;

                width: 450px;

                & > span {
                    height: 25px;

                    border-right: solid 1px #d7d7d7;
                    border-bottom: solid 1px #d7d7d7;
                }

                & > span:first-child {
                    width: 105px;
                }

                & > span:last-child {
                    flex-grow: 1;

                    padding-left: 5px;

                    text-align: left;
                }
            }

            .sys-item:nth-child(2n+2) {
                 & > span {
                    border-left: solid 1px #d7d7d7;
                    border-right: 0;
                }
            }

            .sys-item:nth-child(19) {
                & > span {
                    border-bottom: 0;
                }
            }

            .sys-item:last-child {
                & > span {
                    border-bottom: 0;
                }
            }
        }
    }
}
</style>